/* fonts */
@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-style: normal;
  src: url('./fonts/Roboto-Regular.ttf');
}

@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-style: italic;
  src: url('./fonts/Roboto-Italic.ttf');
}

@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-style: normal;
  src: url('./fonts/Roboto-Medium.ttf');
}

@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-style: italic;
  src: url('./fonts/Roboto-MediumItalic.ttf');
}

@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-style: normal;
  src: url('./fonts/Roboto-Bold.ttf');
}

@font-face {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-style: italic;
  src: url('./fonts/Roboto-BoldItalic.ttf');
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #f5fafb;
}

.btn {
  border: none;
  border-radius: 0.5rem;
  padding: 0.7rem;
  font-size: 1.1rem;
  cursor: pointer;
}

.btn:disabled {
  background-color: #cecece !important;
  cursor: not-allowed;
}
